
body,html{
  font-size:16px;
}
@media screen and (min-width:320px){
body,html{
    font-size: 16px;
}
}
@media screen and (min-width:375px){
body,html{
    font-size: 18px;
}
}
@media screen and (min-width:414px){
body,html{
    font-size: 22px;
}
}
@media screen and (min-width:768px){
body,html{
    font-size: 28px;
}
}
#app {
  width:98%;
  height:90%;
  position: absolute;
  top:1vh;
  left:0.2%;
  padding-top: 7vh;
  margin-bottom: -7vh;
  color: #2c3e50;
  text-align: center;
  overflow: hidden;
  background: -webkit-gradient(linear,left top, left bottom,color-stop(right, top),color-stop(#33a3dc), to(#411445));
  background: linear-gradient(top right,#33a3dc, #411445);
  background: -o-linear-gradient(top right, #33a3dc, #411445);
  -webkit-box-shadow: 2px 6px 5px #411445;
          box-shadow: 2px 6px 5px #411445;
  border-radius:1%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.topbox{
    z-index: 3;
    position: relative;
    width: 20vw;
    height: 20vw;
    left:50%;
    margin-left:-10vw;
    color:#ffe600;
    background: #412f1f;
    border-radius: 50%;
    font-size:1.375rem;
    text-align:center;
    vertical-align: top;
    line-height: 20vw;
}
#time{
    position:relative;
    display: inline-block;
    width: 100%;
    height: 100%;
}
@media screen and (min-width:950px){
body,html{
    font-size: 36px;
}
.topbox{
    z-index: 3;
    position: fixed;
    width: 10vw;
    height: 10vw;
    left:50%;
    top:50%;
    margin-left:-5vw;
    margin-top:-5vw;
    color:#ffe600;
    background: #412f1f;
    border-radius: 50%;
    font-size:1.375rem;
    text-align:center;
    vertical-align: top;
    line-height: 20vw;
}
#time{
      position:relative;
      top:-5vw;
      width:100%;
      height:100%;
}
}
.bodybox{
  width:100vw;
  height: 80vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  overflow-y: auto;
  overflow-x:hidden;
}
.bodybox::-webkit-scrollbar{
  display: none;
}
.leftbox{
  -webkit-box-flex:0;
      -ms-flex:0 0 50vw;
          flex:0 0 50vw;
  width:50vw;
  height:100%;
  padding-bottom:2vh;
  margin-left:-2px;
  border-right:2px dotted rgba(8,255,246,0.12);
}
.rightbox{
  -webkit-box-flex:0;
      -ms-flex:0 0 50vw;
          flex:0 0 50vw;
  width:50vw;
  height:100%;
  padding-bottom:2vh;
}
#stars {
        z-index:-3;
        margin: 0 auto;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;
}
.star {
        z-index:-3;
        display: block;
  　　　width: 1px;
        background: transparent;
        position: relative;
        opacity: 0;
        /*过渡动画*/ 
        animation: star-fall 2s linear infinite;
        -webkit-animation: star-fall 2s linear infinite;
        -moz-animation: star-fall 2s linear infinite;
}
.star:after {
        z-index:-3;
        content: '';
        display: block;
        border: 0px solid #fff;
        border-width: 0px 90px 2px 150px;
        border-color: transparent transparent transparent rgba(255, 255, 255, .5);
        -webkit-box-shadow: 0 0 1px 0 rgba(255, 255, 255, .1);
                box-shadow: 0 0 1px 0 rgba(255, 255, 255, .1);
        /*变形*/       
        transform: rotate(-45deg) translate3d(1px, 3px, 0);
        -webkit-transform: rotate(-45deg) translate3d(1px, 3px, 0);
        -moz-transform: rotate(-45deg) translate3d(1px, 3px, 0);
        transform-origin: 0% 100%;
        -webkit-transform-origin: 0% 100%;
        -moz-transform-origin: 0% 100%;
}
@-webkit-keyframes star-fall {
0% {
          opacity: 0;
          transform: scale(1) translate3d(0, 0, 0);
          -webkit-transform: scale(1) translate3d(0, 0, 0);
          -moz-transform: scale(1) translate3d(0, 0, 0);
}
50% {
          opacity: 1;
          transform: translate3d(-200px, 200px, 0);
          -webkit-transform:- translate3d(-200px, 200px, 0);
          -moz-transform: translate3d(-200px, 200px, 0);
}
100% {
          opacity: 0;
          transform: scale(1) translate3d(-300px, 300px, 0);
          -webkit-transform: scale(1) translate3d(-300px, 300px, 0);
          -moz-transform: scale(1) translate3d(-300px, 300px, 0);
}
}
@keyframes star-fall {
0% {
          opacity: 0;
          transform: scale(1) translate3d(0, 0, 0);
          -webkit-transform: scale(1) translate3d(0, 0, 0);
          -moz-transform: scale(1) translate3d(0, 0, 0);
}
50% {
          opacity: 1;
          transform: translate3d(-200px, 200px, 0);
          -webkit-transform:- translate3d(-200px, 200px, 0);
          -moz-transform: translate3d(-200px, 200px, 0);
}
100% {
          opacity: 0;
          transform: scale(1) translate3d(-300px, 300px, 0);
          -webkit-transform: scale(1) translate3d(-300px, 300px, 0);
          -moz-transform: scale(1) translate3d(-300px, 300px, 0);
}
}
